<template>
  <section id="contact" class="monk-section dons-blue">
    <fieldset>
      <div class="field">
        <label class="label" for="fullName" id="fullNameLabel"> Full Name </label>
        <div class="control has-icons-left has-icons-right">
          <input id="fullName"
                  name="fullName"
                  class="input"
                  v-bind:class="{'is-success': !errors.has('fullName'), 'is-danger': errors.has('fullName')}"
                  type="text"
                  placeholder="Jet Li"
                  v-model="contact.fullName"
                  v-validate="'required|min:3'"/>
          <span class="icon is-small is-left">
            <i class="fas fa-user"></i>
          </span>
          <span v-if="!errors.has('fullName')" class="icon is-small is-right">
            <i class="fas fa-check"></i>
          </span>
          <span v-else class="icon is-small is-right">
            <i class="fas fa-stop"></i>
          </span>
        </div>
      </div>
      <div class="field">
        <label class="label" for="company" id="companyLabel"> Company </label>
        <div class="control has-icons-left has-icons-right">
          <input id="company"
                  name="company"
                  class="input"
                  value="joey-test-company"
                  v-bind:class="{'is-success': !errors.has('company'), 'is-danger': errors.has('company')}"
                  type="text"
                  placeholder="Shaolin Monk"
                  v-model="contact.company"
                  v-validate="'required|min:3'"/>
          <span class="icon is-small is-left">
            <i class="fas fa-building"></i>
          </span>
          <span v-if="!errors.has('company')" class="icon is-small is-right">
            <i class="fas fa-check"></i>
          </span>
          <span v-else class="icon is-small is-right">
            <i class="fas fa-stop"></i>
          </span>
        </div>
      </div>
      <div class="field">
        <label class="label" for="email" id="emailLabel"> E-mail </label>
        <div class="control has-icons-left has-icons-right">
          <input id="email"
                  name="email"
                  class="input"
                  v-bind:class="{'is-success': !errors.has('email'), 'is-danger': errors.has('email')}"
                  type="email"
                  placeholder="jet.li@shaolin.com"
                  v-model="contact.email"
                  v-validate="'required|email'"/>
          <span class="icon is-small is-left">
            <i class="fas fa-envelope"></i>
          </span>
          <span v-if="!errors.has('email')" class="icon is-small is-right">
            <i class="fas fa-check"></i>
          </span>
          <span v-else class="icon is-small is-right">
            <i class="fas fa-stop"></i>
          </span>
        </div>
      </div>
      <div class="field">
        <label class="label" for="phone" id="phoneLabel"> Phone </label>
        <div class="control has-icons-left has-icons-right">
          <input id="phone"
                  name="phone"
                  class="input"
                  v-bind:class="{'is-success': !errors.has('phone'), 'is-danger': errors.has('phone')}"
                  type="tel"
                  placeholder="415.650.1021"
                  v-model="contact.phone"
                  v-validate="'required|min:5'"/>
          <span class="icon is-small is-left">
            <i class="fas fa-envelope"></i>
          </span>
          <span v-if="!errors.has('email')" class="icon is-small is-right">
            <i class="fas fa-check"></i>
          </span>
          <span v-else class="icon is-small is-right">
            <i class="fas fa-stop"></i>
          </span>
        </div>
      </div>
      <div class="field">
        <label class="label"> When do you want to start in Seattle? </label>
        <div class="control">
          <div class="select">
            <select v-model="contact.seattleWhen" name="seattleMove" id="seattleMove">
              <option value="noop" selected="selected"> When? </option>
              <option value="now"> Now! </option>
              <option value="3-6-months"> 3-6 Months </option>
              <option value="12-months"> 12 Months </option>
              <option value="next-year"> Next Year </option>
            </select>
          </div>
        </div>
      </div>
      <div class="field">
        <label class="label"> How do you use Python? </label>
        <div class="control">
          <textarea class="textarea" placeholder="Textarea" name="message" id="message"
                    v-bind:class="{'is-danger': errors.has('message'), 'is-success': !errors.has('message')}"
                    v-model="contact.seattleWhy"
                    v-validate="'required|min:3'"></textarea>
        </div>
      </div>
      <div class="field">
        <div class="control">
          <label class="checkbox">
            <input v-model="contact.sendNda" type="checkbox" name="sendNda" id="sendNda">
            <span> Send me NDA </span>
          </label>
        </div>
      </div>
      <div class="field is-grouped">
        <div class="control">
          <button class="button is-primary" @click="contactDon()"> Contact Don </button>
        </div>
      </div>
    </fieldset>
  </section>
</template>


<script>
import axios from "axios";
const axiosClient = axios.create({
  baseURL: "https://10two1.postforward.io",
  timeout: 3000,
  headers: {
    "Content-Type": "application/json"
  }
});

export default {
  data() {
    return {
      contact: {
        fullName: undefined,
        company: undefined,
        email: undefined,
        phone: undefined,
        seattleWhen: "noop",
        seattleWhy: undefined,
        sendNda: false
      },
      contactDon: function() {
        this.$validator.validate().then(() => {
          if (this.errors.items.length !== 0) {
            return undefined;
          }
          this.contact.sendNda = String(this.contact.sendNda);
          axiosClient
            .post("/contact-don.json", this.contact)
            .then(response => {
              if (this.contact.sendNda === "false") {
                this.contact.sendNda = false;
              } else {
                this.contact.sendNda = true;
              }
              // this.contact.sendNda = bool(this.contact.sendNda)
              // eslint-disable-next-line
              console.log(response);
            })
            .catch(error => {
              // eslint-disable-next-line
              console.error(error);
            });
        });
      }
    };
  }
};
</script>


